.formo-newsticker {
  padding: 1em 0;
  margin: -1em 0!important;
  width: 100%;
  overflow: hidden; 
  display: flex;
  justify-content: flex-start;
  gap: 0;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    display: block;
    left: 0;
    top: 1em;
    bottom: 1em;
    width: 100%;
    background-color: var(--wp--preset--color--formo-yellow, #f3f3f3);
    z-index: 0;
  }
}
.formo-newsticker_content {
  color: var(--wp--preset--color--formo-darktone, #0A0A37);
  font-family: Krana, sans-serif;
  text-transform: uppercase;
  font-size: clamp(20px, 3.9vw, 100px);
  line-height: 1;
  padding: 0.5em 0.5em;
  margin: 0;
  white-space: nowrap;
  min-height: 1em;
  letter-spacing: 0;
  z-index: 1;

  & a {
    color: var(--wp--preset--color--formo-darktone, #0A0A37);
  }
}
@media screen and (max-width: 782px) {
  .formo-newsticker_content {
    font-size: clamp(20px, 8vw, 100px);
  }
}